function shuffleArray(binarArr) {
    for (var i = binarArr.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var temp = binarArr[i];
        binarArr[i] = binarArr[j];
        binarArr[j] = temp;
    }
    return binarArr;
}
var newArray = shuffleArray(binarArr);
